.center-block {
  margin-left: auto;
  margin-right: auto; }

.align--center {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center; }

/**
 * Background Covered
 */
.background--cover {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat; }

.background-image {
  background-size: 100%;
  background-repeat: no-repeat;
  position: relative; }

.background-image::after {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  content: "";
  display: block;
  z-index: -2;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.1; }

/**
 * Flexbox
 */
.align-items--center {
  align-items: center; }

.align-items--end {
  align-items: flex-end; }

.align-items--start {
  align-items: flex-start; }

.justify-content--center {
  justify-content: center; }

/**
 * Misc
 */
.overflow--hidden {
  overflow: hidden; }

.width--50p {
  width: 50%; }

.width--100p {
  width: 100%; }

.z-index--back {
  z-index: -1; }

.max-width--none {
  max-width: none; }

.height--zero {
  height: 0; }

.height--100vh {
  height: 100vh;
  min-height: 15.625rem; }

.height--60vh {
  height: 60vh;
  min-height: 15.625rem; }
