/* ------------------------------------*\
    $FILTER STYLES
\*------------------------------------ */

.filter {
  width: 100% !important;
  z-index: 98;
  margin: 0;

  &.is-active {
    height: 100%;
    overflow: scroll;
    position: fixed;
    top: 0;
    display: block;
    z-index: 999;

    @include media('>large') {
      position: relative;
      top: 0 !important;
      z-index: 98;
    }

    .filter-toggle {
      position: fixed;
      top: 0 !important;
      z-index: 1;
      box-shadow: 0 2px 3px rgba(black, 0.1);

      @include media('>large') {
        position: relative;
      }
    }

    .filter-wrap {
      display: flex;
      padding-bottom: rem(140);

      @include media('>large') {
        padding-bottom: 0;
      }
    }

    .filter-toggle::after {
      content: "close filters";
      background: url('../../assets/images/icon__close.svg') center right no-repeat;
      background-size: rem(15);
    }

    .filter-footer {
      position: fixed;
      bottom: 0;

      @include media('>large') {
        position: relative;
      }
    }
  }

  &.sticky-is-active.is-active {
    @include media('>large') {
      top: rem(40) !important;
    }
  }
}

.filter-is-active {
  overflow: hidden;

  @include media('>large') {
    overflow: visible;
  }
}

.filter-toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  line-height: rem(40);
  padding: 0 $pad;
  height: rem(40);
  background-color: $white;
  cursor: pointer;

  &::after {
    content: "expand filters";
    display: flex;
    background: url('../../assets/images/icon__plus.svg') center right no-repeat;
    background-size: rem(15);
    font-family: $sans-serif;
    text-transform: capitalize;
    letter-spacing: normal;
    font-size: rem(12);
    text-align: right;
    padding-right: rem(25);
  }
}

.filter-label {
  display: flex;
  align-items: center;
  line-height: 1;
}

.filter-wrap {
  display: none;
  flex-direction: column;
  background-color: $white;
  height: 100%;
  overflow: scroll;

  @include media('>large') {
    flex-direction: row;
    flex-wrap: wrap;
    height: auto;
  }
}

.filter-item__container {
  position: relative;
  border: none;
  border-top: 1px solid $border-color;
  padding: $pad;
  background-position: center right $pad;

  @include media('>large') {
    width: 25%;
  }

  &.is-active {
    .filter-items {
      display: block;
    }

    .filter-item__toggle {
      &::after {
        background: url('../../assets/images/arrow__up--small.svg') center right no-repeat;
        background-size: rem(10);
      }

      &-projects::after {
        content: "close projects";
      }

      &-room::after {
        content: "close rooms";
      }

      &-cost::after {
        content: "close cost";
      }

      &-skill::after {
        content: "close skill levels";
      }
    }
  }
}

.filter-item__toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &::after {
    display: flex;
    background: url('../../assets/images/arrow__down--small.svg') center right no-repeat;
    background-size: rem(10);
    font-family: $sans-serif;
    text-transform: capitalize;
    letter-spacing: normal;
    font-size: rem(12);
    text-align: right;
    padding-right: rem(15);

    @include media('>large') {
      display: none;
    }
  }

  &-projects::after {
    content: "see all projects";
  }

  &-room::after {
    content: "see all rooms";
  }

  &-cost::after {
    content: "see all costs";
  }

  &-skill::after {
    content: "see all skill levels";
  }
}

.filter-items {
  display: none;
  margin-top: $space;

  @include media('>large') {
    display: flex;
    flex-direction: column;
    margin-bottom: rem(15);
  }
}

.filter-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: $space-half;
  position: relative;
}

.filter-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  padding: $pad;
  padding-bottom: $pad-half;
  background: $white;
  box-shadow: 0 -0.5px 2px rgba(black, 0.1);

  @include media('>large') {
    flex-direction: row;
    box-shadow: none;
    padding-bottom: $pad;
  }
}

.filter-apply {
  width: 100%;
  text-align: center;

  @include media('>large') {
    min-width: rem(250);
    width: auto;
  }
}

.filter-clear {
  padding: $pad-half $pad;
  font-size: 80%;
  text-decoration: underline;
  border-top: 1px solid $border-color;
  background-color: transparent;
  width: auto;
  color: $gray;
  font-weight: 400;
  box-shadow: none;
  border: none;
  text-transform: capitalize;
  letter-spacing: normal;

  &:hover {
    background-color: transparent;
    color: $black;
  }
}



